<template lang="pug">
    .form-item.form-group
        label 
        .input-group
            span.input-group-btn
                button.btn.btn-primary(@click="addVote()")
                    i.fa.fa-plus
            input(type="text",class="form-control",placeholder="",v-model="input",v-on:keyup.13="addVote()")
        ul.list.well(v-show="voteOptions.length")
            li(v-for="(list,index) in voteOptions")
                input.editable(v-tfocus="editable == index",type="text",class="form-control",:value="list.Title", @blur="saveVote(list.Id)",v-show="editable == index")
                .content.data-tip-top.data-tip-rounded(v-show="editable != index",data-tip="",@dblclick="editVote(list.Id)") {{list.Title}}
                    .close(@click="delVote(list,index)") &times;


</template>

<style lang="sass" scope>
    .list
        margin: 20px 0
        padding: 10px
        list-style: none
        li
            font-size: 16px
            border-radius: 5px
            height: 40px
            padding-left: 8px
            line-height: 40px
            margin-bottom: 10px
            background-color: #fff
            position: relative
            &:last-child
                margin-bottom: 0
            .close
                display: none
                position: absolute
                right: 10px
                top: 8px
            &:hover
                .close
                    display: block
</style>

<script>

    import {Url} from '../Ajax.js';

    export default {
        props:{
            model:Object
        },
        mounted(){
            this.$nextTick(function(){
                let id = this.model.value;
                if(id){
                    this.fetchVoteOption(id);
                }
            })
        },
        data(){
            return {
                input:'',
                editable: null,
                voteOptions: []
            }
        },
        watch:{
            model:function(data){
                console.log(data);
            }
        },
        directives:{
            "tfocus":function(el,obj){
                if(obj.value){
                    setTimeout(function(){
                        el.focus();
                    },100)
                }
            }
        },
        methods:{
            fetchVoteOption(id){
                $.get(Url.vote.list+"?article_id="+id).done((data) =>{
                    this.voteOptions = data.VoteOptions;
                }).fail(function(data){
                    console.log('error');
                });
            },
            addVote(){
                let val = $.trim(this.input);
                if(val){
                    $.post(Url.vote.create,JSON.stringify({
                        'ArticleId': this.model.value,
                        'Description': '',
                        'Title': val
                    })).done((data) => {
                        if(data.Id){
                            this.voteOptions.push(data);
                            this.input = '';
                        }
                        console.log(data);
                    }).fail(function(data){
                        console.log(data);
                    });
                }
            },
            saveVote(){
                this.editable = null;
            },
            delVote(list,index){



                $.post(Url.vote.delete,JSON.stringify({
                    'Id': list.Id
                })).done((data) => {
                    if(!data.ErrorCode){
                        this.voteOptions.splice(index,1);
                    }
                }).fail(function(data){
                    console.log(data);
                });
            },
            editVote(index){
                this.editable = index;
            }
        }
    }

</script>


