<template lang="pug">
    .article
        h1.title {{tabName}}
        ul.nav.nav-tabs
            li(:class="{active:name == 'articleList'}")
                router-link(:to="{ name:'articleList', params: {type:type}}") {{tabName}}
            li(:class=" {active: name == 'articleCreate'}")
                router-link(:to="{name: 'articleCreate', params: {type:type}}") {{tabName}}
        transition(name="slide-fade", mode="out-in")
            router-view

</template>

<script>
    import Model from './model';
    import article from '../../model/article.js';
    // let article = new Article();
    // console.log(article);

    let model = new Model();

    export default {
        watch:{
            $route(r){
                this.tabName = model.getTypeCn(r.params.type);
                this.type  = r.params.type;
                this.name = r.name;
                document.title =  window.title + " - " +model.getTypeCn(r.params.type) + "";
            }
        },
        data(){
            return {
                type : this.$route.params.type,
                tabName: model.getTypeCn(this.$route.params.type),
                name: this.$route.name
            }
        }
    }
</script>


