<template lang="pug">
    nav(aria-label='Page navigation',v-show="page.total > page.limit")
        .btn-group
            button.btn.btn-default(:disabled="page.start == 0",@click.prevent="goTo(0)") 
            button.btn.btn-default(@click.prevent="goTo(currentPage - 2)",v-show="currentPage > 1") 
            button.btn.btn-default(@click.prevent="goTo(currentPage)",v-show="totalPage != currentPage ") 
            button.btn.btn-default(@click.prevent="goTo(totalPage)",:class="{active: currentPage > totalPage}") 
        | &emsp;&emsp;  {{currentPage}} / {{totalPage}}  {{this.page.total}} 

</template>

<style lang="sass">
</style>
<!-- page start limit
1    0     5
2    6     5
3    11    5 -->
<script>
    export default {
        mounted(){
            if(this.$route.query.page > 1){
                this.goTo(this.$route.query.page*1 - 1);
            }
        },
        watch:{
            'currentPage':function(val){
            },
            'page.total':function(val){
                this.totalPage = parseInt((this.page.total / this.page.limit)) ;
                if((this.page.total % this.page.limit) !== 0){
                    this.totalPage ++;
                }
            }
        },
        methods:{
            goTo(page){
                if(page == this.totalPage){
                    page = page - 1;
                }
                this.page.start = page * this.page.limit;
                this.model.fetchList(this.page).done((data) => {
                    this.currentPage = page + 1 ;
                    this.$parent[this.dataListKey] = data[this.dataListKey];
                    this.page.total = data.Total;
                    this.$router.push({name: this.$router.name, query: {page : this.currentPage }});
                });
            }
        },
        data(){
            return {
                currentPage:1,
                totalPage: 0,
                isPrevious: false
            }
        },
        props:{
            model: Object,
            dataListKey: String,
            page: {
                type: Object,
                default: {
                    limit: 5,
                    start: 0,
                    total: 0
                }
            }
        }
    }
</script>


