<template lang="pug">
    span.popup
        button.btn.btn-sm(type='button', :id="id",:data-target="targetId",:class="cls",:data-url="url", @click="handleClick()")
            i(v-show="icon",:class="icon")
            | {{text}}
        .modal.fade-scale(tabindex='-1', role='dialog', aria-labelledby='myModalLabel',:id="targetId")
            .modal-dialog(role='document')
                .modal-content
                    .modal-header
                        button.close(type='button', data-dismiss='modal', aria-label='Close')
                            span(aria-hidden='true') 
                        h4#myModalLabel.modal-title 
                    .modal-body
                        h2
                            i.fa.fa-alert
                            | {{alertMsg}}
                    .modal-footer
                        button.btn.btn-default(type='button', data-dismiss='modal') 
                        button.btn.btn-primary(type='button',@click="handleConfirm") 
</template>

<style lang="sass">
    .modal
        text-align: center
        padding: 0!important
        &:before
            content: ''
            display: inline-block
            height: 100%
            vertical-align: middle
            margin-right: -4px
    .modal-dialog
        display: inline-block
        text-align: left
        vertical-align: middle
    .fade-scale
        transform: scale(0)
        opacity: 0
        transform: scale(0.7);
        transition: all 0.3s;
        &.in
            opacity: 1
            transform: scale(1)
    .modal-backdrop
        transition: all 0.3s
</style>

<script type="text/javascript">

    export default {
        props:{
            targetId : String,
            text     : String,
            url      : String,
            id       : String,
            dataListKey: String,
            params   : Object,
            icon     : String,
            index    : Number,
            alertMsg : {
                type: String,
                default: ''
            },
            cls:{
                type:String,
                default:"btn-warning"
            }
        },
        methods:{
            handleConfirm(){
                console.log(this.$parent.model)
                this.$parent.model.delete(JSON.stringify(this.params)).done((data) =>{
                    // if(!data.ErrorCode){
                    if(!data.ErrCode){
                        $.notify('',{type:'success'});
                        $('#'+this.targetId).modal('hide');
                        this.$parent[this.dataListKey].splice(this.index,1);

                    }
                });
            },
            handleClick(){
                console.log(this)
                $('#'+this.targetId).modal();
            }
        }
    }
</script>



