"use strict";

import {Url} from '../components/Ajax';

let articleTypes = {1:'',2:'',3:''};

class Model {
    constructor(){
        this.url = Url;
        this.defaults = {
            'Id': {
                type: 'text',
                name: 'Id',
                label: 'ID',
                value: ''
            },
            "UserId":  {
                type: 'text',
                name: 'UserId',
                label: 'ID',
                value: ''
            },
            "Type":  {
                type: 'hidden',
                name: 'Type',
                label: '',
                value: ''
            },
            "Status":  {
                type: 'text',
                name: 'Status',
                label: '',
                value: ''
            },
            "Title":  {
                type: 'text',
                name: 'Title',
                label: '',
                error: false,
                errorMsg: '',
                value: ''
            },
            "Topic":  {
                type: 'text',
                name: 'Topic',
                label: '',
                error: false,
                errorMsg: '',
                value: ''
            },
            "Description":  {
                type: 'text',
                name: 'Description',
                label: '',
                error: false,
                errorMsg: '',
                value: ''
            },
            "Author":  {
                type: 'text',
                name: 'Author',
                label: '',
                error: false,
                errorMsg: '',
                value: ''
            },
            "Url":  {
                type: 'text',
                name: 'Url',
                error: false,
                errorMsg: '',
                label: ' ()',
                value: ''
            },
            // "CreateTime":  {
            //     type: 'text',
            //     name: 'CreateTime',
            //     label: '',
            //     value: ''
            // },
            // "UpdateTime":  {
            //     type: 'text',
            //     name: 'UpdateTime',
            //     label: '',
            //     value: ''
            // },
            "Props": {
                "HasChild":true,
                "Content":  {
                    type: 'richText',
                    name: 'Content',
                    label: '',
                    error: false,
                    errorMsg: '',
                    value: ''
                },
                "WebHeaderImg":  {
                    HasChild:true,
                    type: 'image',
                    name: 'WebHeaderImg',
                    label: '',
                    Url:{
                        value:'',
                        error: false,
                        errorMsg: '',
                    },
                    Width:{
                        value:''
                    },
                    Height:{
                        value:''
                    },
                },
                "ShareImg":  {
                    HasChild:true,
                    type: 'image',
                    name: 'ShareImg',
                    label: '',
                     Url:{
                        value:''
                    },
                    Width:{
                        value:''
                    },
                    Height:{
                        value:''
                    },
                },
                "Thumb":  {
                    HasChild:true,
                    type: 'image',
                    name: 'Thumb',
                    label: '',
                    Url:{
                        value:''
                    },
                    Width:{
                        value:''
                    },
                    Height:{
                        value:''
                    }
                }
            }
        };
    }
    get(name){
        return this[name].value;
    }

    getTypeCn(type){

        return articleTypes[type];
    }

    set(name,value){
        this[name] = value;
    }

    fetchList(params){
        let ajax = $.ajax({
            url         : Url.article.list,
            type        : "GET",
            dataType    : "json",
            contentType : "application/json; charset=utf-8",
            data        : params
        });
        return ajax;
    }

    delete(params){
        let ajax = $.ajax({
            url         : Url.article.delete,
            type        : "POST",
            dataType    : "json",
            contentType : "application/json; charset=utf-8",
            data        : params
        });
        return ajax;
    }

    save(params){
        let ajax = $.ajax({
            url         : Url.article.create,
            type        : "POST",
            dataType    : "json",
            contentType : "application/json; charset=utf-8",
            data        : params
        });
        return ajax;
    }

    serializeData(data){
        let temp = {};
        for( var i in data){
            temp[i] = {};
            if(typeof data[i] !== "function"){
                if( typeof data[i] == "object"){
                    temp[i] = this.serializeData(data[i]);
                }else{
                    temp[i].value = data[i];
                }
            }
        }
        return temp;
    }

    unSerializeData(data){

        let temp = {};
        for( var i in data){
            if(typeof data[i] !== "function"){
                temp[i] = {};
                if(typeof data[i].HasChild !== "undefined"){
                    temp[i] = this.unSerializeData(data[i]);
                }else{
                    temp[i] = data[i].value;
                }
                // if( typeof data[i].value !== "undefined"){
                //     temp[i] = data[i].value;
                // }else{
                //     temp[i] = this.unSerializeData(data[i]);
                // }
            }
        }
        return temp;
    }

    toJSONString(model){
        return JSON.stringify(this.unSerializeData(model));
        // return this.unSerializeData(this);
    }
}

export default Model;


