<template lang="pug">
    .article-edit
        div(v-if="$route.params.type != 4")
            field(:model= "model.Title")
            field(:model= "model.Description")
            field(:model= "model.Topic")
            field(:model= "model.Author")
            field(:model= "model.Url")
            field(:model= "model.Status")
            field(:model= "model.Type")
            field(:model= "model.Props.WebHeaderImg")
            field(:model= "model.Props.Thumb")
            field(:model= "model.Props.ShareImg")
            field(:model= "model.Props.Content")
        div(v-else)
            field(:model= "model.Title")
            field(:model= "model.Description")
            field(:model= "model.Topic")
            field(:model= "model.Author")
            field(:model= "model.Status")
            field(:model= "model.Type")
            add-vote-option(:model='model.Id')
            field(:model= "model.Props.WebHeaderImg")
            field(:model= "model.Props.Thumb")
            field(:model= "model.Props.ShareImg")

        .fix-operate
            button.btn.btn-success(@click="savePost()")
                i.fa.fa-save
                | 
</template>


<script>
    import field from '../utils/field.vue';
    import { getToken } from '../utils/common.js';
    import addVoteOption from '../utils/addVoteOption.vue';
    import Model from './model';

    export default {
        name: 'article',
        created(){
            if(!window.UpToken){
                getToken().done((data) => {
                        window.UpToken    = data.Token;
                        window.BucketName = data.Bucket;
                });
            }
        },
        mounted(){
            if(this.$route.name == "articleEdit"){
                this.msg = '';
                this.model.Id.value = this.$route.params.id;
                this.fetchById(this.model.Id.value);
            }
        },
        data(){
            return {
                msg: '',
                model: new Model({
                        'Type': this.$route.params.type*1,
                        'Status': 1
                    })
            }
        },
        watch:{
            $route(r){
                if(r.name == "articleCreate"){
                    this.msg = '';
                    this.model = new Model({
                        'Type': this.$route.params.type*1,
                        'Status': 1
                    });
                }
                console.log(this.model);
            }
        },
        components: { field, addVoteOption },
        methods:{
            fetchById(id){
                this.model.fetchById(id).done((data) => {
                    $.extend(true,this.model,this.model.serializeData(data));
                })
            },
            saveDone(data){
                if(!data.ErrorCode){
                    $.notify({message: this.msg},{type:'success','delay':3000});
                    this.$router.push({name: 'articleList', params: {type : this.$route.params.type }});
                }
            },
            saveServerFail(xhr,error){
                $.notify('',{type:'danger'});
                let errorJSON = JSON.parse(xhr.responseText);
                let errorArr  = errorJSON.ValidationResults;
                if(errorJSON.HttpStatusCode === 400){
                    if(errorArr.length){
                        errorArr.map((i) => {
                            this.model[i.FieldName].error = true;
                            this.model[i.FieldName].errorMsg = i.Message;
                        })
                    }
                }
            },
            savePost(){
                this.model.save(this.model).done(this.saveDone).fail(this.saveServerFail);
            }
        }
    };
</script>


