<template lang="pug">
    .form-group.input-group-lg
        label {{model.label}}
        .upload-wrap
            .upload-btn.btn.btn-primary
                input(type="file",ref='file',v-on:change="upload()")
                i.fa.fa-cloud-upload
                | 
            span.upload-progress {{progress}}
            .upload-preview(v-show="source")
                span(v-if="type == 'image'").preview-img
                    img(:src="source")
                    a.zoom.data-tip-bottom.data-tip-rounded(:href="source",target="_blank",data-tip="")
                        i.fa.fa-search-plus
                audio(:src="source",controls="controls",v-if="type == 'music'")
                input.form-control(type="text",:name="name",ref='selected',v-model="source",@focus="selectAll()")
</template>

<style lang="sass" scoped>
    .upload-btn
        position: relative
        margin-bottom: 10px
        overflow: hidden
        .fa
            margin-right: 5px
    .upload-progress
        margin-left: 15px
        vertical-align: text-bottom
    .upload-preview
        input
            margin-top: 10px
        img
            max-width: 500px
            max-height: 400px
            border: 1px solid #ccc
            object-fit: cover
        .preview-img
            position: relative
            display: inline-block
            &:hover
                .zoom
                    display:block
            .zoom
                color: #fff
                position: absolute
                right: 15px
                font-size: 20px
                bottom: 5px

    .upload-btn input[type="file"]
        position: absolute
        left: 0
        top: 0
        font-size: 200px
        opacity: 0
</style>


<script>

    import QINIU from '../../assets/js/qiniuJsSDK.js';

    export default {
        props:{
            'model':Object,
            "label":String,
            "name":String,
            "btnText":String,
            "bucketName":{
                type:String,
                default:"si00"
            },
            "uptoken":{
                type: String,
                default: ''
            },
            "type":{
                type:String,
                default:"image"
            },
            "extensions":{
                type:Array,
                default(){
                    return [];
                }
            }
        },
        data(){
            return {
                qiniu:'',
                source: this.value,
                file:"",
                'value':'',
                selected:"",
                progress: '',
                exts: ["png", "jpg", "jpeg", "gif", "bmp", "mp3", "mp4","wav"]
            }
        },
        mounted(){
            this.source = this.model.Url.value;
        },
        methods:{
            upload(){

                let self       = this;
                let file       = this.$refs.file.files[0];
                let fileKey    = QINIU.generateRandomFileKey(file.name);
                let requestTag = this.$refs.file.tagName;
                let token      = window.UpToken;
                let bucketName = "http://ohr25ovqc.bkt.clouddn.com";

                if (!QINIU.isExtension(file.name, this.exts.concat(this.extensions))) {
                    $.notify('',{type:'danger'});
                    return false;
                }

                console.log(file);

                QINIU.uploadFile(file, token, fileKey, {

                    requestTag: requestTag,
                    progressCallback(e, done, total, current) {
                        self.progress = current+"%";
                    },
                    uploadComplete(e, xhr) {

                        self.progress = '';
                        let resp = jQuery.parseJSON(xhr.responseText);
                        let url  = bucketName+"/" + fileKey;;

                        self.source = self.value = url;

                        if (QINIU.isImage(url)) {
                            let image = new Image();
                            image.src = url;
                            image.onload = function(){
                                self.model.Url.value    = url;
                                self.model.Width.value  = resp.w*1;
                                self.model.Height.value = resp.h*1;
                                self.model.Format.value = resp.mime_type;
                            };
                            // self.addImgInfo(resp);
                        } else if (QINIU.isHTML5Video(url) || QINIU.isMusic(url)) {

                          let duration = parseInt(QINIU.getDuration(url));
                          duration = isNaN(duration) ? 0 : duration;
                        }

                    },
                    uploadFailed(xhr) {
                        self.source = false;
                        $.notify('',{type:'danger'});
                    },
                    uploadCanceled(e, xhr) {
                    }
                });
            },
            // meow-type 1   2   
            addImgInfo(imgInfo){

                if(this.meowId === 1 && this.name == "img_url"){
                    $("input[name='img_width']").val(imgInfo.w);
                    $("input[name='img_height']").val(imgInfo.h);
                }

                if(this.meowId === 2 && this.name == "bg_url"){
                    $("input[name='bg_width']").val(imgInfo.w);
                    $("input[name='bg_height']").val(imgInfo.h);
                }

            },
            selectAll(){
                this.$refs.selected.select();
            }
        },
        watch:{
            'model.Url.value':function(val){
                this.source = this.model.Url.value;
            },
            source(val){
                if(this.type == "music"){
                    let url = val + '?avinfo';
                    $.ajax({
                        url: url,
                        type: 'get',
                        success: function(d) {
                            $('input[name="music_duration"]').val(d['streams'][0]['duration']);
                        }
                    });
                }
            }
        }
    }
</script>


