<template lang="pug">
    .jspatch
        h1.title  Patch 
        .form-group
            input(type="text",class="form-control",placeholder="ZIP",v-model="input",v-on:keyup.13="jsPatch()")
        .form-group
            button.btn.btn-primary(@click="jsPatch()") 
</template>


<script>
    import {isUrl} from './utils/utils';
    export default {
        data(){
            return {
                input: ''
            }
        },
        methods:{
            jsPatch(){
                let val = $.trim(this.input);
                if(isUrl(val)){
                    $.notify('',{type:'success'});
                    console.log(val);
                }else{
                    $.notify('',{type:'danger'});
                }
            }
        }
    }

</script>


