<template lang="pug">
    .spinner(v-show="loading")
            span.loading
                i.fa.fa-spinner
            |  {{txt}}
</template>

<style lang="sass">
    @keyframes spin
            to
                -webkit-transform: rotate(1turn)
    .spinner
        color: #666
        text-align: center
        font-size: 30px
        margin-top: 60px
    .loading
        display: inline-block
        animation: spin 1s linear infinite
</style>

<script>
    export default {
        props:{
            loading: false,
            txt: {
                type: String,
                default: ""
            }
        }
    }
</script>


