<template lang="pug">
    .form-item
        .form-group(v-if="model.type == 'text'", :class="{'has-error': model.error}")
            label.control-label {{model.label}}
            input(:name="model.name",type="text",:id="id",:class="cls",v-model="model.value",@change="changeModel").form-control
            span.help-block(v-show="model.error") {{model.errorMsg}}
        .form-group(v-if="model.type == 'select'")
            label {{model.label}}
            template(v-if="model.name == 'Status'")
                select(:name="model.name",:id="id",:class="cls",v-model="model.value").form-control
                    option(v-for=" o in status", :value="o") {{ o | toStatusCn}}
        .form-group(v-if="model.type == 'image'")
            upload(:model="model")
        .form-group(v-if="model.type == 'richText'")
            richTextEdit(:model="model")
        .form-group(v-if="model.type == 'cropImg'")
            crop-img(:model="model",label="(400X400)",:configs='{aspectRatio:1/1}')
        div(v-if="model.type == 'hidden'")
            input(:label="model.label",:name="model.name",:type="model.type",:id="id",:class="cls",:model="model.value",v-if="model.type == 'hidden'").form-control

</template>

<style lang="sass">

</style>

<script>

    import upload       from './upload.vue';
    import richTextEdit from './richTextEdit.vue';
    import cropImg      from './cropImg.vue';

    export default {
        name: 'field',
        components:{upload, richTextEdit, cropImg},
        mounted(){
        },
        methods:{
            changeModel(){
                if(this.model.value){
                    this.model.error = false;
                }
            }
        },
        watch:{
        },
        data(){
            return {
                v: this.model.value,
                status: [1,2,4]
            }
        },
        props:{
            id    : String,
            model : Object,
            cls  : String
        }
    }

</script>


