<template lang="pug">
    .container
        .row
            .col-md-4.col-md-offset-4
                .login-panel.panel.panel-default
                    .panel-heading
                        h3.panel-title StarCity 
                    .panel-body
                        fieldset
                            .form-group
                                label 
                                input(type="text",class="form-control",placeholder="",v-model="username",v-on:keyup.13="login()")
                            .form-group
                                label 
                                input(type="password",class="form-control",placeholder="",v-model="password",v-on:keyup.13="login()")
                            .form-group
                                input.btn-block.btn.btn-success(value=' ^_^ !',@click="login()")

</template>

<style lang="sass">
    .container
        margin-top: 10%
    .login-page
        background: #05080D url(http://mi0.mmmono.com/2be8618f9cc798a1521f96b49073dc8d.jpg) center center no-repeat
        background-size: cover
        .navbar
            display: none
        #page-wrapper
            margin: 0
            background-color: transparent

</style>

<script>

    export default {
        mounted(){
            document.querySelector('body').classList.add('login-page');
        },
        destroyed(){
            document.querySelector('body').classList.remove('login-page');
        },
        data(){
            return {
                username: '18612566404',
                password: 'fuck',
            }
        },
        methods:{
            login(){

                let username = $.trim(this.username);
                let password = $.trim(this.password);

                if(username && password){
                    $.ajax({
                        url         : Url.common.login,
                        type        : "POST",
                        dataType    : "json",
                        contentType : "application/json; charset=utf-8",
                        data        : JSON.stringify({
                            'PhoneNumber': username,
                            'Password':password,
                            'CountryCode':'+86'
                        })
                    }).done((data) =>{
                        if(data.ErrorCode == 0){
                            $.notify('',{type:'success'});
                            setTimeout(() =>{
                                this.$router.push('/');
                            },1000);
                        }
                        console.log(data);
                    }).fail(function(data){
                        $.notify('',{type:'danger'});
                        // console.log(data)
                    });
                    console.log(username);
                    console.log(password);
                    // window.location.reload();
                }else{
                    $.notify('',{type:'danger'});
                }

            }
        }
    }

</script>


