"use strict";

let articleTypes = {1:'',2:'',3:'',4:''};

const Model = function(options){

    let defaults = {
        'Id': {
            type: 'text',
            name: 'Id',
            label: 'ID',
            value: ''
        },
        "UserId":  {
            type: 'text',
            name: 'UserId',
            label: 'ID',
            value: ''
        },
        "Type":  {
            type: 'hidden',
            name: 'Type',
            label: '',
            value: ''
        },
        "Status":  {
            type: 'select',
            name: 'Status',
            label: '',
            value: ''
        },
        "Title":  {
            type: 'text',
            name: 'Title',
            label: '',
            error: false,
            errorMsg: '',
            value: ''
        },
        "Topic":  {
            type: 'text',
            name: 'Topic',
            label: '',
            error: false,
            errorMsg: '',
            value: ''
        },
        "Description":  {
            type: 'text',
            name: 'Description',
            label: '',
            error: false,
            errorMsg: '',
            value: ''
        },
        "Author":  {
            type: 'text',
            name: 'Author',
            label: '',
            error: false,
            errorMsg: '',
            value: ''
        },
        "Url":  {
            type: 'text',
            name: 'Url',
            error: false,
            errorMsg: '',
            label: ' ()',
            value: ''
        },
        // "CreateTime":  {
        //     type: 'text',
        //     name: 'CreateTime',
        //     label: '',
        //     value: ''
        // },
        // "UpdateTime":  {
        //     type: 'text',
        //     name: 'UpdateTime',
        //     label: '',
        //     value: ''
        // },
        "Props": {
            "HasChild":true,
            "Content":  {
                type: 'richText',
                name: 'Content',
                label: '',
                error: false,
                errorMsg: '',
                value: ''
            },
            "WebHeaderImg":  {
                HasChild:true,
                type: 'cropImg',
                name: 'WebHeaderImg',
                label: '',
                Url:{
                    value:'',
                    error: false,
                    errorMsg: '',
                },
                Width:{
                    value:''
                },
                Height:{
                    value:''
                },
                Format:{
                    value:''
                }
            },
            "ShareImg":  {
                HasChild:true,
                type: 'image',
                name: 'ShareImg',
                label: '',
                 Url:{
                    value:''
                },
                Width:{
                    value:''
                },
                Height:{
                    value:''
                },
                Format:{
                    value:''
                }
            },
            "Thumb":  {
                HasChild:true,
                type: 'image',
                name: 'Thumb',
                label: '',
                Url:{
                    value:''
                },
                Width:{
                    value:''
                },
                Height:{
                    value:''
                },
                Format:{
                    value:''
                }
            }
        }
    };

    $.extend(true,this,defaults,this.serializeData(options));
};

Model.prototype.get = function(name){
    return this[name].value;
};

Model.prototype.getTypeCn = function(type){

    return articleTypes[type];
};

Model.prototype.set = function(name,value){
    this[name] = value;
};

Model.prototype.fetchList = function(params){
    let ajax = $.ajax({
        url         : Url.article.list,
        type        : "GET",
        dataType    : "json",
        contentType : "application/json; charset=utf-8",
        data        : params
    });
    return ajax;
};

Model.prototype.delete = function(params){
    let ajax = $.ajax({
        url         : Url.article.delete,
        type        : "POST",
        dataType    : "json",
        contentType : "application/json; charset=utf-8",
        data        : params
    });
    return ajax;
};

Model.prototype.save = function(params){

    let url = Url.article.create;
    if(params.Id.value){
        url = Url.article.update;
    }
    let ajax = $.ajax({
        url         : url,
        type        : "POST",
        dataType    : "json",
        contentType : "application/json; charset=utf-8",
        data        : this.toJSONString(params)
    });
    return ajax;
};

Model.prototype.fetchById = function(id){
    let ajax = $.ajax({
        url         : Url.article.get + "/" + id,
        type        : "GET",
        dataType    : "json",
        contentType : "application/json; charset=utf-8"
    });
    return ajax;
};

Model.prototype.serializeData = function(data){
    let temp = {};
    for( var i in data){
        temp[i] = {};
        if(typeof data[i] !== "function"){
            if( typeof data[i] == "object"){
                temp[i] = this.serializeData(data[i]);
            }else{
                temp[i].value = data[i];
            }
        }
    }
    return temp;
};

Model.prototype.unSerializeData = function(data){

    let temp = {};
    for( var i in data){
        if(typeof data[i] !== "function"){
            temp[i] = {};
            if(typeof data[i].HasChild !== "undefined"){
                temp[i] = this.unSerializeData(data[i]);
            }else{
                temp[i] = data[i].value;
            }
            // if( typeof data[i].value !== "undefined"){
            //     temp[i] = data[i].value;
            // }else{
            //     temp[i] = this.unSerializeData(data[i]);
            // }
        }
    }
    return temp;
};

Model.prototype.toJSONString = function(model){
    return JSON.stringify(this.unSerializeData(model));
    // return this.unSerializeData(this);
};

export default Model;


