<template lang="pug">
    .article-list
        loading(:loading="loading")
        table.table.table-striped.table-hover.table-bordered.table-responsive(v-show="!loading")
            thead
                tr
                    th ID
                    th 
                    th 
                    th 
                    th 
                    th 
            tbody(v-show="page.total == 0")
                tr
                    td(colspan="8",align="center") {{typeCn}}
            //- tbody(is="transition-group", name="list-complete")
            tbody
                //- tr(v-show="page.total == 0")
                //-     td(colspan="8",align="center") {{typeCn}}
                tr(v-for = "article,index in Articles")
                    td {{article.Id}}
                    td {{article.Title}}
                    td
                        .h4
                            span.label.label-info {{article.Status | toStatusCn}}
                    td {{article.Author}}
                    td {{article.CreateTime | formatDate('YY-MM-DD hh:mm') }}
                    td
                        .btns
                            router-link.btn.btn-sm.btn-primary(:to="{ name: 'articleEdit',params:{'id':article.Id}}") 
                            confirm-pop(data-list-key="Articles",:index="index",:id="'del_'+ article.Id",:target-id="'delete-popup-' + article.Id",text="",:params="{'id':article.Id}")
                            a.btn.btn-sm.btn-info() 
        pagination(:page="page",:model="model",data-list-key="Articles")
</template>

<style lang="sass">
    .list-complete-item
        color: #000
        transition: all 1s;
        display: inline-block;
        margin-right: 10px;
    .list-complete-enter, .list-complete-leave-active
        opacity: 0
        transform: translateY(30px)
    .list-complete-leave-active
        position: absolute
</style>

<script>

    import Model from './model';
    import pagination from '../utils/pagination.vue';
    import confirmPop from '../utils/popup/confirm.vue';

    export default {
        data(){
            return {
                loading  : false,
                Articles : null,
                type     : this.$route.params.type,
                status   : this.$route.query.status || 1,
                model    : new Model(),
                typeCn   : '',
                page     : {
                    total  : 0,
                    limit  : 7,
                    start  : 0,
                    type   : this.$route.params.type,
                    status : this.$route.query.status || 1
                }
            }
        },
        components: { pagination,confirmPop },
        watch:{
            $route(r){
                this.type   = r.params.type;
                this.typeCn = this.model.getTypeCn(this.$route.params.type);
                document.title =  window.title + " - " + this.typeCn + "";
                this.featchData();
            }
        },
        created(){
            this.featchData();
        },
        mounted(){
            this.typeCn = this.model.getTypeCn(this.$route.params.type);
        },
        methods:{
            featchSuccess(data){
                this.Articles   = data.Articles;
                this.page.total = data.Total;
            },
            featchData(){
                this.loading = true;
                let params = {
                    'type'   : this.type,
                    'status' : this.status,
                    'start' : this.page.start,
                    'limit'  : this.page.limit,
                }
                this.model.fetchList(params).done(this.featchSuccess).fail((data)=>{

                }).always((data) =>{
                    this.loading  = false;
                });
            }
        }
    }
</script>


