"use strict";

import VueRouter from 'vue-router';
import DashBoard from '../components/DashBoard.vue';

Vue.use(VueRouter);

const routes = [
    { path: '/', component: DashBoard },
    { path: '/login', component: require('../components/Login.vue') },
    { path: '/article', component: require('../components/article/index.vue'),
        children: [
            {
                name: 'articleList',
                path: ':type/list',
                component:require('../components/article/list.vue')
            },
            {
                name: 'articleCreate',
                path: ':type/create',
                component:require('../components/article/edit.vue')
            },
            {
                name: 'articleEdit',
                path: ':type/edit/:id',
                component:require('../components/article/edit.vue')
            }
        ]
    },
    { name: 'jsPatch', path: '/jsPatch', component: require('../components/jsPatch.vue') },
];

const router = new VueRouter({ routes });


export default router;


