<template lang="pug">
    .vue-compontent.form-group
        .crop-img.input-item
            label.form-label {{label}}PNGJPGGIF
            input(name="${name}",value="${value}",type="hidden")
            .upload-pic.upload-file
                input(type="file",ref="file").file
                button.btn.btn-primary.upload-btn
                    i.fa.fa-cloud-upload
                    |  
                span.progress(v-show="cropping") ...
                span.progress(v-show="uploading") ...
            .preview.fn-dib(v-show="value")
                img(v-bind:src="value")
                span.bg(@click="preview_edit()",v-show="uploadType !== 'gif'")
                    i.g-icon-edit
        .crop-img-wrap(v-show="preview_src",ref="popup")
            img(:src="preview_src",ref="img")
            .crop-option
                span.zoom
                    span.crop-add(@click="crop_add()")
                        i.fa.fa-plus
                    | &ensp;
                    span.crop-minus(@click="crop_minus()")
                        i.fa.fa-minus
                span.crop-confirm.btn.btn-primary(@click="handleConfirmBtn()") 
                | &emsp;
                span.crop-cancel.btn.btn-default(data-remodal-action="close") 
</template>

<style lang="sass" scoped>
    input[type="file"]
        position: absolute
        left: 0
        top: 0
        font-size: 200px
        opacity: 0
        cursor: pointer
    .progress
        margin-left: 15px
    .upload-pic
        max-width: 170px
        display: block !important
        overflow: hidden
        margin-bottom: 10px
        position: relative
        cursor: pointer
    .preview
        margin-top: 0px !important
        img
            max-width: 300px
    .crop-img-wrap
        height: 500px
        overflow: hidden
    .remodal
        padding: 15px 15px 65px
        margin-bottom: 0
    .crop-option
        margin-top: 15px
        .zoom
            margin-right: 30px
            span
                margin-left: 10px
                cursor: pointer
            .fa
                font-size: 16px
                
            
</style>

<script type="text/javascript">

    require('remodal-js');
    require('remodal-css');
    require('remodal-theme-css');

    if(typeof $.fn.cropper !== "function"){
        require('cropper-js');
        require('cropper-css');
    }

    let config = {
        props:{
            'model': Object,
            'label':String,
            'name':String,
            'value':{
                type: String,
                default(){
                    return '';
                }
            },
            'configs':{
                type:Object,
                default(){
                    return {}
                }
            },
            'url':{
                type:String,
                default:"/admin_v2/editor/upload_image"
            }
        },
        data(){
            return {
                file:'',
                files:'',
                $modal:'',
                isModify:false,
                tip:false,
                uploadType:"pic",
                uploading:false,
                cropping:false,
                preview_src:this.value
            }
        },
        methods:{
            renderImgData(file){
                let reader = new FileReader();
                reader.onload = () =>{
                    this.preview_src = reader.result;
                    this.initCropImg();
                };
                reader.readAsDataURL(file);
            },
            initCropImg(){


                let crop_config = {
                    aspectRatio: 9 / 5,
                    autoCropArea: 0.8,
                    exts:['png','jpg','gif']
                    // strict: true,
                    // autoCrop:false,
                    // viewMode:1,
                    // guides: false,
                    // highlight: false,
                    // mouseWheelZoom:false,
                    // scalable: false,
                    // dragCrop: false,
                    // resizable: true,
                    // cropBoxResizable: false
                };
                crop_config = $.extend(crop_config,this.configs);
                if(this.file){
                    let ext = this.file.name.toLowerCase().split(/[\s.]+/).pop();
                    if(crop_config.exts.indexOf(ext) == -1){
                        alert('PNGJPGGIF');
                        return false;
                    }
                }
                this.$modal.open();
                setTimeout(() =>{
                    $(this.$refs.img).cropper(crop_config);
                },100);
                
                this.$file.val('');
            },
            getImgBlobData(image,callback){

                let img = new Image();
                img.setAttribute('crossOrigin', 'anonymous');
                img.src = this.preview_src;

                let dataURItoBlob = function(dataURI){
                    let byteString;
                    if (dataURI.split(',')[0].indexOf('base64') >= 0){
                        byteString = atob(dataURI.split(',')[1]);
                    }else{
                        byteString = unescape(dataURI.split(',')[1]);
                    }
                    // separate out the mime component
                    let mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];

                    // write the bytes of the string to a typed array
                    let ia = new Uint8Array(byteString.length);
                    for (let i = 0; i < byteString.length; i++) {
                        ia[i] = byteString.charCodeAt(i);
                    }
                    return new Blob([ia], {type:mimeString});
                };

                img.onload = function(){

                    let canvas = document.createElement("canvas");
                    canvas.width = image.naturalWidth;
                    canvas.height = image.naturalHeight;
                    let ctx = canvas.getContext('2d');
                    ctx.drawImage(img,0,0);
                    let base64data = canvas.toDataURL();
                    let fileBlob = dataURItoBlob(base64data);
                    callback(fileBlob);
                }
            },
            handleConfirmBtn(){
                let self = this;
                let crop_data = $(this.$refs.img).cropper("getData");
                let image = $(this.$refs.img).cropper('getImageData');
                let zoomLevel = image.width / image.naturalWidth || 0;
                let crop_url = this.url+"?x="+crop_data.x+"&y="+crop_data.y+"&w="+crop_data.width+"&h="+crop_data.height+"&z="+zoomLevel;

                this.cropping = true;
                this.$modal.close();
                if(this.isModify){
                    this.getImgBlobData(image,function(fileBlob){
                        self.xhrCropImg({
                            url:crop_url,
                            file:fileBlob,
                            success:function(){},
                            error:'',
                            abort:''
                        });
                    });
                }else{
                    this.xhrCropImg({
                        url:crop_url,
                        file:this.file,
                        success:function(){},
                        error:'',
                        abort:''
                    });
                }
            },
            xhrCropImg(options){

                let self = this;
                let xhr = new XMLHttpRequest();
                let onProgressListener = function(e) {
                    let done     = e.position || e.loaded;
                    let total    = e.totalSize || e.total;
                    let progress = Math.floor(done/total*1000)/10;
                };
                let fd = new FormData();
                fd.append('upfile', options.file);
                $.ajax({
                    url: options.url,
                    data: fd,
                    processData: false,
                    contentType: false,
                    type: 'POST',
                    success(data){
                        let img = $("<img >").attr("src",data.file_path);
                        img.one("load",() =>{
                            self.cropping = false;
                            self.preview_src = self.value = data.file_path
                            options.success(data);
                        });

                    },
                    fail:function(e,xhr){
                        self.cropping = false;
                        options.error(e, xhr);
                    }
                });
            },
            crop_add(){
                let image = $(this.$refs.img).cropper('getImageData');
                let zoomLevel = image.width / image.naturalWidth;
                if(zoomLevel >= 2){
                    // $(this.$refs.img).cropper("zoom",0);
                }else{
                    $(this.$refs.img).cropper("zoom",0.1);
                }
            },
            crop_minus(){
                $(this.$refs.img).cropper("zoom",-0.1);
            },
            preview_edit(){
                if(this.uploadType !== "gif"){
                    this.preview_src = this.value;
                    this.isModify = true;
                    this.file = '';
                    this.initCropImg();
                }
            },
            isGif(v){
                // console.log(v);
                if(v.split(".").reverse()[0] === "gif"){
                    return true;
                }else{
                    return false;
                }

            },
            uploadGif(file){
                let reader  = new FileReader();
                let self    = this;
                reader.addEventListener("load", function () {
                    let image  = new Image();
                    image.addEventListener("load", function () {
                        let w     = image.width;
                        let h     = image.height;
                        let size  = Math.round(file.size/1024);
                        let ratio = w/h;

                        if(w/h <= 2 && w/h >= 0.5 && size <= 2048){
                            self.uploading = true;
                            self.xhrCropImg({
                                url:self.url,
                                file:file,
                                success:function(){
                                    self.uploading = false;
                                },
                                error:'',
                                abort:''
                            });
                        }else{
                            alert("GIF2:12M");
                        }
                    });
                    image.src = reader.result;
                });
                reader.readAsDataURL(file);
            }
        },
        watch:{
            file(new_val){

                if(new_val){

                    this.renderImgData(new_val);
                }
            }
        },
        mounted(){

            let self = this;

            this.$file  = $(this.$el).find(".file");
            this.$modal = $(this.$refs.popup).remodal();

            console.log(this.$modal);

            $(this.$refs.file).on("change",function(){
                if(self.isGif(this.files[0].name)){
                    self.uploadGif(this.files[0]);
                    self.uploadType = 'gif';
                }else{
                    self.file = this.files[0];
                    self.isModify = false;
                    self.uploadType = 'pic';
                }
            });
            $(document).on('closed', '.remodal', function (e) {
                $(self.$els.img).cropper('destroy');
            });

            if(this.isGif(this.value)){
                this.uploadType = "gif";
            }
        }
    };
    export default config;
</script>



